<template>
	<div>
		<input
			type="file"
			@change="importExcel"
			accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
		/>
		<div>
			<div v-for="(item, idx) in items" :key="idx">
				<div v-for="(data, idx) in item" :key="data. + idx">
					<h2
						style="
    color: dodgerblue;
    font-weight: bold;
    font-size: 30px;
"
					>
						{{ idx + 1 }}
					</h2>
					<h1>{{ data. }}</h1>
					<table>
						<tr>
							<th></th>
							<td>{{ data. }}</td>
						</tr>
						<tr>
							<th></th>
							<td>{{ data. }}</td>
						</tr>
						<tr>
							<th></th>
							<td>
								<textarea
									v-html="data."
									style="
    width: 100%;
    height: 200px;
"
								></textarea>
							</td>
						</tr>
						<tr>
							<th>url</th>
							<td><a :href="data.URL" target="_blank"></a></td>
						</tr>
					</table>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import XLSX from 'xlsx';
export default {
	data() {
		return {
			items: [],
			tmpResult: '',
		};
	},
	methods: {
		readFile(event) {
			// get File object from input tag
			const file = event.target.files[0];
			// const fileName = file.name; // declare FileReader, temp result
			const reader = new FileReader();
			let tmpResult = {}; // if you use "this", don't use "function(e) {...}"
			reader.onload = e => {
				let data = e.target.result;
				data = new Uint8Array(data); // get excel file
				let excelFile = XLSX.read(data, { type: 'array' }); // get prased object
				excelFile.SheetNames.forEach(function(sheetName) {
					const roa = XLSX.utils.sheet_to_json(excelFile.Sheets[sheetName], { header: 1 });
					if (roa.length) tmpResult[sheetName] = roa;
				});
				this.result = tmpResult.Sheet1;
			};
			reader.readAsArrayBuffer(file);
		},
		importExcel(event) {
			const file = event.target.files[0];
			let reader = new FileReader();
			reader.onload = e => {
				let data = e.target.result;
				data = reader.result;
				let workbook = XLSX.read(data, { type: 'binary' });
				workbook.SheetNames.forEach(sheetName => {
					const roa = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
					this.items.push(roa);
				});
			};
			reader.readAsBinaryString(file);
		},
	},
};
</script>

<style>
table {
	width: 100%;
	border: 1px solid #444444;
}
th,
td {
	border: 1px solid #444444;
}
</style>
